.page-body {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.page-body > h3 {
  font-size: 2rem;
}

.project-card-container {
  display: flex;
  justify-content: center;
  padding: 2rem;
  gap: 20px;
  flex-wrap: wrap;
}

.project-card {
  background-color: #99ceff;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 1rem;
  border-radius: 20px;
  flex: 1 0 32.1%;
  max-width: 400px;
}

.project-card > h4 {
  margin: 1rem 0;
  font-size: 1.5rem;
  color: #0084ff;
}

.links-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}

.links-container > a {
  display: flex;
  align-items: center;
  background-color: #0084ff;
  color: white;
  padding: 10px 40px;
  text-decoration: none;
  border-radius: 20px;
}

.link-icon {
  height: 20px;
  margin-right: 5px;
}

.project-card > img {
  margin: 0 auto;
  max-width: 80px;
}

.project-card > p {
  color: #3279bb;
  line-height: 1.8;
  font-weight: 600;
  font-size: 1.1rem;
}
